  display: flex;
  flex-direction: column;
  font-size: 14px;
  justify-content: center;
  > div {
    flex: 1;
    padding-left: 18px;
    line-height: 25px;
    a {
      text-decoration: none;
      color: #333;
    }
    span {
      font-size: 12px;
      color: #636363;
    }
    a:hover {
      color: #c81623;
    }
  }
}
.nav-side {
  width: 1000px;
  height: 470px;
  border: 1px solid #f7f7f7;
  box-sizing: border-box;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  position: absolute;
  top: 10px;
  left: 190px;
  z-index: 999;
  transition: top 0.25s ease;
  font-size: 50px;
  line-height: 470px;
}
#thirdnav {
  position: relative;
}
</style>
