<template>
  <div class="nicegoodsbar">
    <div class="nicegoods-center w">
      <div class="nicegoods-left">
        <a href="javascript:;">
          <div>
            <div>&nbsp;&nbsp;<i class="fas fa-angle-right"></i></div>
          </div>
        </a>
      </div>
      <div class="nicegoods-right">
        <ul>
          <li><img src="../assets/img/nbanner0.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner1.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner2.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner3.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner4.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner0.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner1.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner2.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner3.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner4.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner0.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner1.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner2.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner3.webp" alt="" /></li>
          <li><img src="../assets/img/nbanner4.webp" alt="" /></li>
        </ul>
        <div class="ngscroll"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Nicegoodsbar",
  mounted() {
  let nicegoodsbar = document.querySelector(".nicegoodsbar");
  let ngbanner = nicegoodsbar.querySelector(".nicegoods-right");
  let ngul = nicegoodsbar.querySelector("ul");
  let ngulW = ngul.offsetWidth;
  let ngbannerW = ngbanner.offsetWidth;
  let ngscroll = nicegoodsbar.querySelector(".ngscroll");
  // console.log(ngulW);
  // console.log(ngul.scrollLeft);

  //
  function ani2(obj, target, callback) {
    // 
    clearInterval(obj.timerr);
    obj.timerr = setInterval(() => {
      let step = -2; // 
      // 
      step = step > 0 ? Math.ceil(step) : Math.floor(step);
      //
      if (obj.offsetLeft == target) {
        clearInterval(obj.timerr);
        /* if (callback){
          callback();
        } */
        callback && callback();
      }
      // console.log(obj.offsetLeft);
      obj.style.left = obj.offsetLeft + step + "px";
    }, 15);
  }
  //
  function ngMove() {
    let sMax = ngbannerW - ngscroll.offsetWidth;
    let ulMax = ngulW - ngbannerW;
    // left0  
    if (ngul.style.left == -ngulW + ngbannerW + "px") {
      ngul.style.left = 0;
      //
      ngscroll.style.left = 0;
    }
    // =  *  /   
    ngscroll.style.left = -(parseInt(ngul.style.left) * sMax) / ulMax + "px";
    // console.log(": " + ngul.style.left);
    ani2(ngul, -ngulW + ngbannerW);
    // console.log(ngscroll.style.left);
    // console.log(sMax);
    // console.log(ulMax);
  }
  //
  ngbanner.addEventListener("mouseenter", function () {
    //  
    clearInterval(timer1);
    clearInterval(ngul.timerr);
    timer1 = null;
    ngscroll.style.display = "block";
  });
  // 
  ngbanner.addEventListener("mouseleave", function () {
    timer1 = setInterval(() => {
      ngMove();
    }, 30);
    ngscroll.style.display = "none";
  });
  // 
  let timer1 = setInterval(() => {
    ngMove();
  }, 30);

  // 
  ngscroll.addEventListener("mousedown", function () {
    let fnnn = function (e) {
      let eX = e.pageX - ngbanner.offsetLeft; //
      let x = e.pageX - e.offsetX;
      console.log(x);
      let sMax = ngbannerW - ngscroll.offsetWidth; //
      let sX = eX - ngscroll.offsetWidth / 2;
      //  
      if (sX <= 0) {
        sX = 0;
      } else if (sX >= sMax) {
        sX = sMax;
      }
      //
      ngscroll.style.left = sX + "px";
      let ulMax = ngulW - ngbannerW;
      let ulX = (sX * ulMax) / sMax;
      ngul.style.left = -ulX + "px";
    };
    nicegoodsbar.addEventListener("mousemove", fnnn);
    nicegoodsbar.addEventListener("mouseup", function () {
      nicegoodsbar.removeEventListener("mousemove", fnnn);
    });
  });
 },
};
</script>

<style lang="less">
.nicegoodsbar {
  margin-bottom: 20px;
  height: 260px;
  .nicegoods-center {
    height: 260px;
    display: flex;
    .nicegoods-left {
      height: 100%;
      width: 190px;
      a {
        display: block;
        background-image: url(../assets/img/nicegoods1.png);
        height: 100%;
        width: 100%;
        text-decoration: none;
        color: #ffffffcc;
        font-size: 16px;
        > div {
          padding-top: 15px;
          div {
            margin: 0 auto;
            width: 140px;
            height: 23px;
            line-height: 23px;
            background-image: url(../assets/img/nicegoods2.png);
            padding-top: 55px;
            padding-left: 20px;
            padding-bottom: 10px;
          }
        }
      }
      a:hover i {
        color: #e1251b;
      }
    }
    .nicegoods-right {
      margin-left: 10px;
      background-color: #fff;
      width: 990px;
      height: 100%;
      overflow: hidden;
      position: relative;
      ul {
        position: absolute;
        height: 100%;
        width: calc(15 * 198px);
        left: 0;
        li {
          float: left;
          width: 198px;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      .ngscroll {
        position: absolute;
        left: 0;
        bottom: 20px;
        width: 50px;
        height: 10px;
        background-color: #999;
        border-radius: 10px;
        display: none;
      }
    }
  }
}
</style>
